import * as React from "react";
import { useEffect, useState } from "react";
import { graphql, type HeadFC, type PageProps } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Company } from "../interfaces";
import data from "../data/data.json";
import Layout from "./layout";
import images from "../images";
import Carousel from "../components/carousel";
import Collapsible from "../components/collapsible";

const info: Company = data[0];
type IconKey = "seo_icon_1" | "seo_icon_2" | "seo_icon_3" | "seo_icon_4";

const IndexPage: React.FC<PageProps> = () => {
  const { t } = useTranslation();
  const items = info.homePage.items;

  const carouselImages = [
    { src: images.bg_carousel_1.src, alt: images.bg_carousel_1.alt },
    { src: images.bg_carousel_2.src, alt: images.bg_carousel_2.alt },
    { src: images.bg_carousel_3.src, alt: images.bg_carousel_3.alt },
    { src: images.bg_carousel_4.src, alt: images.bg_carousel_4.alt },
  ];

  const textOverlay = [
    { content: t("carousel.text_1"), positionClass: `${styles.slide1}` },
    { content: t("carousel.text_2"), positionClass: `${styles.slide2}` },
  ];

  useEffect(() => {
    document.body.style.backgroundImage = `url(${images.bg.src})`;
    document.body.style.backgroundSize = "cover";
    document.body.style.backgroundPosition = "center";
    document.body.style.backgroundRepeat = "no-repeat";

    return () => {
      document.body.style.backgroundImage = "";
      document.body.style.backgroundSize = "";
      document.body.style.backgroundPosition = "";
      document.body.style.backgroundRepeat = "";
    };
  }, []);

  return (
    <>
      <Layout data={info}>
        <section className="flex w-full mx-auto relative">
          <Carousel
            images={carouselImages}
            textOverlay={textOverlay}
            speed={6000}
          />
        </section>

        <section
          className="my-8 sm:my-32 max-w-[1024px] mx-auto"
          id={info?.menu[0]}
        >
          <h1 className={styles.sectionTitles}>{t("keywords.advancedTech")}</h1>
          <h4 className={styles.subtitles}>{t("servicesPage.subtitle_1")}</h4>
          <div className="border-2 w-full sm:w-[1024px] mx-auto rounded-xl p-2 sm:p-8 mt-8 bg-darkBg/50">
            <Collapsible development={info.servicesPage.web} />
          </div>
        </section>

        <section
          className="my-8 sm:my-32 max-w-[1024px] mx-auto"
          id={info?.menu[1]}
        >
          <h2 className={styles.sectionTitles}>
            {t("keywords.specificExemples")}
          </h2>
          <h4 className="text-md text-center my-4 sm:my-0">
            {t("homePage.portfolio_copy")}
          </h4>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-2 sm:gap-8 w-full mx-auto mt-0 sm:mt-8 rounded-xl p-2 sm:p-8 border-2 bg-darkBg/50">
            {info.homePage.portfolio.map((site, index) => {
              return (
                <div
                  className=" rounded-xl overflow-hidden relative"
                  key={index}
                >
                  <a href={site.link} target="_blank">
                    <h3 className="absolute right-0 bottom-0 text-md bg-purplesemidark p-2 rounded-tl-lg font-semibold text_shadow">
                      {site.title}
                    </h3>
                    {site.image && (
                      <div className="item">
                        <img
                          src={images[site.image as keyof typeof images]?.src}
                          alt={
                            images[site.image as keyof typeof images]?.alt ||
                            site.title
                          }
                          className="w-full h-auto"
                        />
                      </div>
                    )}
                  </a>
                </div>
              );
            })}
          </div>
        </section>

        <section
          className="flex flex-col w-full sm:w-[1024px] mx-auto mt-2 sm:mt-36 py-8"
          id={info?.menu[2]}
          style={{
            backgroundImage: `url(${images.bg_workflow.src})`,
            backgroundSize: "cover",
          }}
        >
          <h3 className={styles.sectionTitles}>{t("keywords.optimization")}</h3>
          <div className="flex flex-col sm:flex-row justify-between gap-8 px-8">
            {items.map((item, index) => {
              const iconKey = `seo_icon_${index + 1}` as keyof typeof images;
              return (
                <div key={index} className="flex flex-col w-full">
                  <div className="text-center text-2xl font-bold my-2 sm:my-8">
                    {t(`homePage.${item.title}`)}
                  </div>
                  {item.copy && (
                    <>
                      <div className="text-center border-2 bg-darkBg/50 rounded-lg p-8 flex-1 flex w-full flex-col">
                        <img
                          className={styles.icon}
                          src={images[iconKey].src}
                          alt={images[iconKey].alt}
                        />
                        {t(`homePage.${item.copy}`)}
                      </div>
                    </>
                  )}
                </div>
              );
            })}
          </div>
        </section>
      </Layout>
    </>
  );
};

export default IndexPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  sectionTitles: "text-2xl sm:text-4xl text-center poppins-regular text-verde",
  slide1: "top-2/3 left-[10%] sm:left-1/2",
  slide2: "top-1/3 left-[10%]",
  subtitles: "text-md text-center my-4 sm:my-0",
  icon: "w-16 h-16 mx-auto mb-4 opacity-30",
};
